<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                </p>
        </section>
        <div class="con-con">
            <div class="data-title">
                <div class="left">
                    <div class="title"></div>
                    <div class="date">{{ beginTime }}</div>
                    <div class="week">{{ beginWeek }}</div>
                </div>
                <div class="middle">
                    
                </div>
                <div class="right">
                    <div class="title"></div>
                    <div class="date">{{ endTime }}</div>
                    <div class="week">{{ endWeek }}</div>
                </div>
            </div>
            <Calendar
                    :months="months"
                    :value="value"
                    :now="false"
                    :begin="noBegin"
                    :disabled="begin"
                    :responsive="false"
                    :lunar="false"
                    clean
                    :range="true"
                    :monthRange="monthRange"
                    @select="selected"
                    ref="calendar"
            />
        </div>
        <md-button type="default" @click="sendDate" size="small" inline></md-button>
    </main>
</template>

<script>
import { Field, CellItem, Toast, Icon, InputItem, Button, Tabs, TabPane } from 'mand-mobile'
import Calendar from 'mpvue-calendar'
import 'mpvue-calendar/src/browser-style.css'
import { getNowDay } from '../../../utils/public'
import { sendDate } from '../../../apiUrl/personal/server'
export default {
  name: 'index',
  components: {
    Calendar,
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Tabs.name]: Tabs,
    [TabPane.name]: TabPane,
    [InputItem.name]: InputItem
  },
  data () {
    return {
      months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      // disabledArray: ['2018-6-27','2018-6-25'],
      value: [],
      begin: ['2019-9-4'],
      noBegin: [],
      beginTime: '',
      endTime: '',
      beginWeek: '',
      endWeek: '',
      end: [], // 20
      monthRange: ['2019-9', '2019-12']
    }
  },
  watch: {
    beginTime (old, news) {
      console.log(old, news)
      this.beginWeek = '' + ' '.charAt(new Date(old).getDay())
    },
    endTime (old, news) {
      console.log(old, news)
      this.endWeek = '' + ' '.charAt(new Date(old).getDay())
    }
  },
  mounted () {
    var myDate = new Date()
    var nowY = myDate.getFullYear()
    var nowM = myDate.getMonth() + 1
    var nowD = myDate.getDate()
    this.noBegin.push(nowY)
    this.noBegin.push(nowM)
    this.noBegin.push(nowD)
  },
  methods: {
    // 
    goBack () {
      this.$router.push('/personal')
    },
    sendDate () {
      const args = {
        id: this.$route.query.id,
        status: 1,
        dockingStartTime: this.beginTime,
        dockingEndTime: this.endTime
      }
      sendDate(args).then((res) => {
        Toast.succeed('')
        this.$router.push('/personal/server')
      })
    },
    selected (val, val2) {
      this.beginTime = val.join('-')
      this.endTime = val2.join('-')
      console.log(this.$refs.calendar.monthRangeDays)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        overflow hidden
        margin-bottom 0px
    .top
        .title
            background url("../../../assets/personal/bg.png") no-repeat
    .con-con
        margin 0px auto
        margin-top 88px
        margin-bottom 60px
        .data-title
            height 260px
            padding 30px
            .left
                width:230px;
                height:180px;
                float left
                padding 10px 34px
                text-align left
                color #fff
                background:rgba(2,25,85,1);
                border-radius:8px;
                .title
                    font-size 28px
                    line-height 50px
                .date
                    font-size 32px
                    line-height 60px
                    font-weight:bold;
                .week
                    font-size 28px
                    line-height 50px
            .middle
                width 156px
                float left
                color #021955
                font-size 28px
                line-height 50px
                margin 60px 0px 0px 45px
                border-bottom 1px solid #021955
            .right
                width:230px;
                height:180px;
                float right
                padding 10px 34px
                text-align left
                color #929292
                .title
                    font-size 28px
                    line-height 50px
                .date
                    font-size 32px
                    line-height 60px
                    font-weight:bold;
                .week
                    font-size 28px
                    line-height 50px
    .datePicker
        /*background-color:#f7f8fd;*/
        padding-top: 85px;
        height: 100%
    /deep/.mc-range-second-to-last
        background none
    /deep/.month-rang-head
        text-align center
        line-height 50px
        font-size 32px
    /deep/.md-button
        position fixed
        bottom 0
        left 0
        z-index 111
        width 100%
        height 98px
        font-size 36px
        background:rgba(2,25,85,1);
    /deep/.default:after
        border none
</style>
